div.record {
	margin-top: 2rem;
	width: 100%;
	max-height: 40vh;
	display: flex;
	background: rgba(0, 0, 0, 0.3);
	flex-direction: column;
}

div.record-container{
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	border-bottom: 1px solid;
}


h2.record-amount {
	max-width: 20%;
	border-right: 1px solid;
	height: fit-content;
	max-height: 10vh;
	overflow-y: scroll;
}

p.record-comment {
	width: 70%;
	margin-left: auto;
	display: flex;
	flex-direction: column;
	height: fit-content;
	max-height: 10vh;
	overflow-y: scroll;
	word-break: break-all;
	font-size: 1.3rem;
}

.addDiv{
	position: absolute;
	top: 10%;
	left: 5%;
	width: 55%;
	height: 30%;
	border: 1px solid black;
	display: flex;
	flex-direction: column;
	justify-content:flex-start;
	align-items: center;
	row-gap: 0.5rem;
	background: rgba(0, 0, 0, 0.6);
	border-radius:10px;
}

.addH2, .addH2Comment {
	font-size: 1.3rem;
	text-align: center;
	padding: 0.9rem 0.7rem;
	padding-bottom: 0.2rem;
}

.addInput, {
	width: 30%;
	min-height: 1.5rem;
}

.addInputComment {
	width: 70%;
	min-height: 2rem;
}

.addButtonComment {
	margin-top: 2rem;
	width: 13%;
	height: 10%;
	border-radius: 5px;
	box-shadow: 0px 5px 10px;
}

.button-click {
	animation-name: light;
	animation-duration: 1s;
}

@keyframes light {
	1% {
		box-shadow: none;
	} 10% {
		box-shadow: 0px 2px 10px yellow;
	} 25% {
		box-shadow: 0px 10px 20px yellow;
	} 50% {
		box-shadow: 0px 15px 30px yellow;
	} 75% {
		box-shadow: 0px 20px 40px yellow;
	} 100% {
		box-shadow: 0px 25px 50px yellow;
	}
}

.add {
	background: green;
	width: 25%;
}

.minus {
	background: red;
	width: 25%;
}