div.balance-block {
	margin-top: 2rem;
	width: 45%;
	height: 45vh;
	display: flex;
	justify-content: center;
}

div.balance-container {
	width: 90%;
	height: 100%;
	display: flex;
	flex-direction: column;
	border: 1px solid rgba( 255, 255, 255, 0.18 );
	background: rgba( 255, 255, 255, 0.3 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    border-radius: 15px;
}

div.balance-header {
	width: 100%;
	display: flex;
	justify-content: center;
}

h3.balance {
	font-size: 1.4rem;
}

div.import-container {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	column-gap: 1rem;
}

div.balance-image {
	width: 15%;
	font-size: 5rem;
}

div.balance-amount {
	width: 60%;
}

p.balance {
	font-size: 3rem;
	text-align: center;
	border: 1px solid rgba( 255, 255, 255, 0.18 );
	background: rgba( 255, 255, 255, 0.3 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    border-radius: 15px;
}