div.user-block{
	margin-top: 2rem;
	width: 45%;
	height: 45vh;
	/*background: green;*/
	display: flex;
	justify-content: center;
}

div.user-container{
	width: 50%;
	height: 100%;
	display: flex;
	flex-direction: column;
	border: 1px solid rgba( 255, 255, 255, 0.18 );
	background: rgba( 255, 255, 255, 0.3 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    border-radius: 15px;
}

div.user-image, div.user-name {
	width: 100%;
	display: flex;
}

div.user-image{
	justify-content: center;
	align-items: flex-end;
	height: 60%;
}

div.user-name{
	justify-content: center;
	align-items: center;
	height: 40%;
}

div.user-border-image{
	width: 50%;
	border: 1px solid black;
	border-radius: 25px 25px 20px 20px;
	text-align: center;
}

div.user-border-image:hover {
	box-shadow: 0px 6px 20px rgba(200, 200, 50, 0.9) ;
	border: none;
	cursor: pointer;
}

div.user-image > * {
	font-size: 6rem;
}

